@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&family=Ubuntu:wght@400;500;700&display=swap");
.nav{
  position: fixed;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0.7rem 2rem;
  overflow: hidden;
  z-index: 999;
  width: 100%;
  margin: auto;
  background-color: #DC143C;
  color:#FFFFFF;
  top: 0;
  border-bottom: solid 1px var(--primary-color);
  opacity: 0.9;
  top: 0;
  transition: all 0.3s ease;
}
.Nav{
  display:flex;
  justify-content:space-between;
   gap:20px;
}

.name{
  color:#FFFFFF;
  font-size:40px;
  font-family:"Ubuntu,sans-sarif"

}
/* fist intro css */
.intro{
  position: relative;
 
}
.nameIntro{
  position: absolute;
  top: 200px;
  left: 200px;
  color:#FFFFFF;
  /* border: 1px solid red; */
  text-align: left;
  height: fit-content;
  height: 40vh;
  background-attachment: fixed;
  
}
.p-1{
  font-family: "Ubuntu",sans-serif;
  font-size: 40px;
 
}
.h-1{
  font-family: "Ubuntu",sans-serif;
  font-size: 70px;
  
}
.resume{
  width: 30%;
  height: 50px;
  border-radius: 10px;
  background-color: #DC143C;
  color: #FFFFFF;
  font-size: 30px;
  font-family: "Ubuntu",sans-serif;
  margin-left: 100px;
  text-align: center;
}



@media (min-width: 300px) and (max-width: 700px) {
  .nav{
    background-color: #DC143C;
    justify-content: space-between;
    color: #FFFFFF;
    
  }
   .Nav{
    justify-content:space-between;
   gap:2px;
  }
   
  .name{
    color: #FFFFFF;
  }
   .img{
    height: 400px;
   }
  .nameIntro{
    position: absolute;
    top: 150px;
    left: 40px;
  }
  .p-1{
    font-family: "Ubuntu",sans-serif;
    font-size: 20px;
   
  }
  .h-1{
    font-family: "Ubuntu",sans-serif;
    font-size: 40px;
    
  }
  .resume{
    width: 50%;
    /* height: ; */
    margin-left: 50px;
  }
  
}